.hero__container {
  display: flex;
  align-items: flex-start;
}
@media (max-width: 767px) {
  .hero__container {
    flex-direction: column-reverse;
  }
}
.hero__content {
  padding: 100px 0;
  width: 50%;
  max-width: fit-content;
  will-change: auto;
  transform: skew(0, 10deg);
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .hero__content {
    padding-top: 60px;
  }
}
@media (max-width: 767px) {
  .hero__content {
    width: 100%;
  }
}
@media (max-width: 533px) {
  .hero__content {
    padding-top: 30px;
  }
}
.hero__content .main-title {
  font-size: clamp(30px, 5vw, 60px);
}
.hero__content .main-title,
.hero__content .main-desc {
  max-width: 580px;
}
@media (max-width: 767px) {
  .hero__content .main-title,
  .hero__content .main-desc {
    max-width: 100%;
  }
}
.hero__side {
  width: 50%;
  height: 100%;
}
.hero__side-wrapper {
  position: relative;
  width: 100%;
  background-size: cover;
  transform: translateX(100px) skew(10deg, 0deg);
}
.hero__side-wrapper::before {
  position: absolute;
  top: -15%;
  left: -30%;
  content: "";
  width: 140%;
  min-width: 800px;
  height: 120%;
  transform: rotate(-5.8deg);
  background-image: url(../assets/img/hero-overlay-2.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left;
  z-index: 10;
}
@media (max-width: 1280px) {
  .hero__side-wrapper::before {
    top: -50%;
    left: -150px;
    min-width: 800px;
    height: 175%;
    transform: rotate(15deg);
  }
}
@media (max-width: 767px) {
  .hero__side-wrapper::before {
    top: -5vw;
    left: -20vw;
    width: 100vw;
    min-width: 400px;
    height: 120%;
    transform: rotate(-5deg);
  }
}
@media (max-width: 1023px) {
  .hero__side-wrapper {
    transform: translateX(70px) skew(10deg, 0deg);
  }
}
@media (max-width: 533px) {
  .hero__side-wrapper {
    transform: translateX(55px) skew(10deg, 0deg);
  }
}
.hero__side video {
  display: block;
  width: 68vw;
  min-width: 1000px;
  max-width: 1285px;
  height: auto;
  object-fit: cover;
  -webkit-mask-image: url("../assets/img/svg-mask.svg");
  -webkit-mask-position: center center;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: cover;
  mask-image: url("../assets/img/svg-mask.svg");
  mask-position: center left;
  mask-repeat: no-repeat;
  mask-size: cover;
}
@media (max-width: 767px) {
  .hero__side video {
    width: 100vw;
    min-width: 400px;
  }
}

.features {
  position: relative;
  width: 100%;
  height: 100%;
  margin-top: -150px;
  z-index: 11;
  will-change: transform;
}
.features__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 24px;
  height: 100%;
}
@media (max-width: 1023px) {
  .features__list {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 12px;
  }
}
@media (max-width: 767px) {
  .features__list {
    grid-template-columns: 1fr;
  }
}
.features__list-item {
  align-items: center;
  padding: 2vw 1vw;
  background-color: var(--white);
  border-radius: 20px;
  border: 1px solid var(--white);
  background-color: var(--white);
  box-shadow: 0px 65px 140px 0px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .features__list-item {
    align-items: flex-start;
    justify-content: center;
    min-height: 140px;
    position: relative;
    padding-left: 100px;
  }
}
@media (max-width: 533px) {
  .features__list-item {
    padding: 15px;
    padding-left: 75px;
  }
}
.features__list-item img {
  margin-bottom: 14px;
}
@media (max-width: 1023px) {
  .features__list-item img {
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
  }
}
@media (max-width: 533px) {
  .features__list-item img {
    width: 45px;
    height: 45px;
    left: 15px;
  }
}
.features__list-item-title {
  display: inline-block;
  font-size: clamp(16px, 1.5vw, 20px);
  font-weight: 600;
  text-align: center;
  margin-bottom: 14px;
}
@media (max-width: 1023px) {
  .features__list-item-title {
    text-align: left;
  }
}
.features__list-item-text {
  display: inline-block;
  font-size: clamp(14px, 1.3vw, 18px);
  text-align: center;
}
@media (max-width: 1023px) {
  .features__list-item-text {
    text-align: left;
  }
}

.about {
  padding: 60px 0 15vw;
}
@media (max-width: 360px) {
  .about {
    padding: 60px 0;
  }
}
@media (min-width: 1240px) {
  .about {
    padding: 60px 0 120px;
  }
}
.about__side {
  display: flex;
  height: fit-content;
  position: sticky;
  top: 120px;
}
.about__side img {
  margin: 0 auto;
}
@media (max-width: 767px) {
  .about__side img {
    position: static;
    top: unset;
  }
}
.about__list-item {
  font-size: clamp(14px, 2vw, 18px);
}
.about__list-item:not(:last-child) {
  margin-bottom: 30px;
}
.about__list-item p {
  margin: 0;
}
.about__list-item span {
  display: inline-block;
  padding: 5px;
  border-radius: 5px;
  background-color: var(--secondary);
}

.support {
  margin: 0;
}
.support__content .main-title span {
  display: block;
  font-size: clamp(14px, 2vw, 20px);
  text-transform: uppercase;
  margin-bottom: 12px;
}
.support__side {
  display: flex;
  height: fit-content;
  position: sticky;
  top: 120px;
}
.support__side img {
  margin: 0 auto;
}

.services {
  position: relative;
  padding: 10vw 0 0;
  z-index: 1;
}
@media (max-width: 360px) {
  .services {
    padding: 60px 0 0;
  }
}
@media (min-width: 1500px) {
  .services {
    padding: 200px 0;
  }
}
.services__container {
  position: relative;
}
.services__container::before {
  position: absolute;
  bottom: 0;
  left: -15vw;
  content: "";
  width: 60vw;
  height: 100%;
  max-height: 760px;
  background-image: url("../assets/img/services-figure.svg");
  background-size: contain;
  background-repeat: no-repeat;
  z-index: -1;
}
.services .main-title {
  text-align: center;
  margin-bottom: 75px;
}
.services__content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .services__content {
    grid-template-columns: 1fr;
    grid-gap: 5px;
  }
}

.advantages {
  position: relative;
  overflow: hidden;
  margin: 0;
  background-color: transparent;
}
.advantages #particles {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  transform: skew(0, 10deg);
}
.advantages .main-title {
  text-align: center;
}
.advantages__container {
  max-width: 1440px;
}
.advantages__list {
  gap: 24px;
  justify-content: center;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 18px;
  }
}
@media (max-width: 767px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 1fr;
  }
}
.advantages__list-item {
  align-items: center;
  width: calc(33.3333333333% - 16px);
  padding: 10px;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .advantages__list-item {
    align-items: flex-start;
    width: 100%;
    text-align: start;
    padding-left: 90px;
    position: relative;
  }
}
@media (max-width: 1023px) {
  .advantages__list-item {
    width: 100%;
  }
}
.advantages__list-item img {
  margin-bottom: 16px;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .advantages__list-item img {
    position: absolute;
    top: 60px;
    left: 0;
  }
}
@media (max-width: 1023px) {
  .advantages__list-item img {
    width: 70px;
    height: 70px;
  }
}
.advantages__list-item-title {
  font-size: clamp(16px, 2vw, 18px);
  font-weight: 600;
  margin-bottom: 12px;
}
.advantages__list-item .main-text, .advantages__list-item-title {
  display: inline-block;
  text-align: center;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .advantages__list-item .main-text, .advantages__list-item-title {
    text-align: left;
  }
}
.advantages__list-item .main-text {
  margin-bottom: 0;
}

.reviews {
  margin-top: 0;
}
.reviews .main-title {
  text-align: center;
  margin-bottom: 0;
}
.reviews__container {
  display: flex;
  justify-content: center;
  position: relative;
  margin-bottom: 20px;
}
@media (max-width: 533px) {
  .reviews__container {
    justify-content: space-between;
  }
}
@media (min-width: 534px) {
  .reviews .container-fluid {
    padding-right: 0;
  }
}
.reviews__content {
  display: flex;
  gap: 10px;
  width: 100%;
}

.faq {
  position: relative;
}
.faq.skew-section {
  padding: 0;
  background-color: transparent;
}
.faq__container {
  display: grid;
  grid-template-columns: 4fr 7fr;
  grid-gap: 5vw;
  padding-top: clamp(150px, 36vw, 400px);
  padding-bottom: clamp(150px, 36vw, 400px);
  overflow-x: hidden;
}
@media (max-width: 1023px) {
  .faq__container {
    grid-template-columns: 1fr;
  }
}
.faq__side {
  position: relative;
  z-index: 2;
  padding-top: 150px;
}
.faq__side .main-text {
  margin: 0;
}
@media (max-width: 1023px) {
  .faq__side {
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .faq__side .main-text,
  .faq__side .main-title {
    text-align: center;
  }
}
.faq__content {
  position: relative;
  z-index: 2;
}
.faq__label {
  position: absolute;
  font-size: clamp(150px, 36vw, 400px);
  line-height: 1;
  font-weight: 600;
  will-change: scroll-position;
  color: var(--gray-10);
  z-index: 0;
}
.faq__label--top {
  top: clamp(38px, 9vw, 100px);
  right: -100vw;
}
.faq__label--bottom {
  bottom: clamp(38px, 9vw, 100px);
  left: -100vw;
}

.offer-wrapper::before {
  position: absolute;
  bottom: 130px;
  right: 10%;
  width: 100%;
  max-width: 35vw;
  min-width: 270px;
  height: 100%;
  max-height: 35vw;
  min-height: 270px;
  content: "";
  background-image: url("../assets/img/offer-section-figure.svg");
  background-size: contain;
  background-repeat: no-repeat;
  z-index: -1;
  will-change: transform;
}
@media (max-width: 533px) {
  .offer-wrapper::before {
    bottom: 210px;
  }
}

.offer {
  margin-bottom: clamp(60px, 8vw, 150px);
}
.offer .main-title {
  font-size: clamp(24px, 5vw, 50px);
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  margin-bottom: 40px;
}
.offer .main-title span {
  display: inline-block;
  padding: 3px;
  background-color: var(--secondary);
}
.offer .offer-form {
  max-width: 1240px;
  margin: 0 auto;
}

.service-content .main-title {
  text-align: center;
}
.service-content__list {
  gap: 16px;
  max-width: 1080px;
  margin: 0 auto;
  padding-left: 32px;
}
.service-content__list-item {
  position: relative;
  padding: clamp(15px, 2vw, 25px) clamp(25px, 3vw, 40px);
  border-radius: 10px;
  border: 1px solid var(--white);
  background-color: var(--white);
}
.service-content__list-item-title {
  display: inline-block;
  margin-bottom: 4px;
  font-size: clamp(16px, 2vw, 20px);
}
.service-content__list-item-text {
  font-size: clamp(14px, 1.5vw, 16px);
  color: var(--gray-70);
}
.service-content__list-item::before {
  position: absolute;
  top: 50%;
  left: -16px;
  transform: translateY(-50%);
  width: 32px;
  height: 32px;
  content: "";
  background-image: url("../assets/img/icon-service-check.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.service-stuff {
  background-color: var(--white);
}
.service-stuff .main-title {
  text-align: center;
}
.service-stuff__list {
  gap: 20px;
  justify-content: center;
  max-width: 1260px;
  margin: 0 auto;
  margin-bottom: clamp(40px, 5vw, 90px);
}
.service-stuff__list-item {
  display: flex;
  justify-content: center;
  width: calc(50% - 10px);
}
@media (max-width: 767px) {
  .service-stuff__list-item {
    width: 100%;
  }
  .service-stuff__list-item:not(:last-child) {
    position: relative;
  }
  .service-stuff__list-item:not(:last-child)::before {
    position: absolute;
    bottom: -35px;
    right: 50%;
    transform: translateX(50%);
    content: "";
    width: 51px;
    height: 51px;
    background-image: url("../assets/img/icon-plus.svg");
    background-repeat: no-repeat;
    background-size: 21px;
    background-position: center;
    background-color: var(--primary);
    border-radius: 50%;
    box-shadow: -18px 57px 103.4px 0px #3d006f;
    z-index: 10;
    /* up/down desktop-lg(1920),desktop(1440),laptop(1140),tablet-lg(960),tablet(768),mobile-lg(534)*/
  }
}
@media (max-width: 767px) and (max-width: 533px) {
  .service-stuff__list-item:not(:last-child)::before {
    width: 41px;
    height: 41px;
    bottom: -31px;
  }
}
@media (min-width: 768px) {
  .service-stuff__list-item--related {
    position: relative;
  }
  .service-stuff__list-item--related::before {
    position: absolute;
    top: -35px;
    right: -35px;
    content: "";
    width: 51px;
    height: 51px;
    background-image: url("../assets/img/icon-plus.svg");
    background-repeat: no-repeat;
    background-size: 21px;
    background-position: center;
    background-color: var(--primary);
    border-radius: 50%;
    box-shadow: -18px 57px 103.4px 0px #3d006f;
  }
  .service-stuff__list-item--related-position-top::before {
    top: -35px;
    right: 50%;
    transform: translateX(50%);
  }
  .service-stuff__list-item--related-position-right::before {
    top: 50%;
    right: -35px;
    transform: translateY(-50%);
  }
}
.service-stuff__note {
  font-weight: 600;
  text-align: center;
  max-width: 840px;
  margin: 0 auto;
}

